:root {
    --first: #fff;
    --second: #e4e5f1;
    --third: #9394a5;
    --text: #333;
}

body {
    margin: 0;
    color: var(--text);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: var(--first);
}

h1, h3 ,h4 {
    margin: 0;
}

h2 {
    margin: 0 0 10px;
}

h3, h4, h5 {
    font-weight: 500;
    word-break: break-word;
}

h5 {
    color: var(--third);
    margin: 3px 0 7px;
} 

.container {
    padding-bottom: 10px;
}

.topbar, .active-section, .archive-section {
    max-width: 1000px;
    margin: 0 auto;
}

input, textarea {
    border: none;
    background-color: var(--second);
    border-radius: 5px;
}

input, textarea, button {
    font-size: 1rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    outline: none;
    padding: 10px 15px;
}

button {
    font-weight: 700;
    background-color: transparent;
    border: 1px solid var(--third);
    cursor: pointer;
}

header {
    padding: 10px 15px;
    border-bottom: 1px solid var(--third);
    position: sticky;
    top: 0;
    background-color: var(--first);
}

.topbar {
    display: flex;
}

.topbar h1 {
    flex-grow: 1;
}

.topbar input {
    width: 250px;
}

.add-section {
    max-width: 500px;
    padding: 10px 15px;
    margin: 10px auto;
}

form {
    display: grid;
    row-gap: 10px;
}

.add-section button {
    border-radius: 5px;
}

.add-section h4 {
    text-align: right;
    color: var(--third);
}

.active-section, .archive-section {
    padding: 10px 15px;
}

ul {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.text {
    overflow: auto;
    padding: 10px 15px;
    height: 230px;
    border: 1px solid var(--third);
    border-radius: 5px 5px 0 0;
    border-bottom: none !important;
}

.btn-archive {
    color: rgb(205, 207, 65);
    width: 50%;
    border-radius: 0 0 0 5px;
    border-right: none !important;
}

.btn-delete {
    color: rgb(199, 23, 23);
    width: 50%;
    border-radius: 0 0 5px 0;
}

.empty-text {
    color: var(--third);
    text-align: center;
    border: 1px solid var(--third);
    border-radius: 5px;
    padding: 10px 15px;
}

@media screen and (max-width: 700px) {
    ul {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 550px) {
    .topbar {
        display: grid;
    }

    .topbar input {
        width: 100%;
        margin-top: 5px;
    }
    
    ul {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 400px) {
    ul {
        grid-template-columns: repeat(1, 1fr);
    }
}